body {
  min-width: 320px;
  font-family: "Lato", sans-serif;
}
body::-webkit-scrollbar {
  width: 7px;
  background: #3D4050;
}
body::-webkit-scrollbar-thumb {
  width: 7px;
  background: #3E9CDC;
  border-radius: 10px;
}

.container {
  margin: 0 auto;
  padding: 0 30px;
  max-width: 1232px;
}

.stop-scroll {
  overflow: hidden;
}

@media screen and (max-width: 468px) {
  .container {
    padding: 0 15px;
    max-width: 1202px;
  }
}
.logo__icon {
  width: inherit;
  height: inherit;
}

.burger-menu {
  width: 20px;
  height: 20px;
  position: relative;
  z-index: 3;
  overflow: hidden;
  display: none;
}
.burger-menu:hover {
  cursor: pointer;
}
.burger-menu__item {
  width: 20px;
  height: 2px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #fff;
  transition: all 0.25s;
}
.burger-menu__item:nth-of-type(2) {
  top: calc(50% - 6px);
}
.burger-menu__item:nth-of-type(3) {
  top: calc(50% + 6px);
}

@media screen and (max-width: 768px) {
  .burger-menu {
    display: block;
  }
  .burger-menu__item.active:nth-of-type(1) {
    display: none;
  }
  .burger-menu__item.active:nth-of-type(2) {
    top: 50%;
    transform: translate(-50%, 0%) rotate(45deg);
  }
  .burger-menu__item.active:nth-of-type(3) {
    top: 50%;
    transform: translate(-50%, 0%) rotate(-45deg);
  }
}
@media screen and (max-width: 468px) {
  .burger-menu {
    width: 17px;
    height: 17px;
  }
  .burger-menu__item {
    width: 17px;
  }
  .burger-menu__item:nth-of-type(2) {
    top: calc(50% - 5px);
  }
  .burger-menu__item:nth-of-type(3) {
    top: calc(50% + 5px);
  }
}
.button {
