.form {
  position: relative;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-areas: "select-menu email name" "slider slider file" ". button .";
  gap: 30px;
  width: 100%;
}
.form__input {
  padding: 12px 10px;
  width: 100%;
  height: 100%;
  font-size: 18px;
  font-weight: 400;
  line-height: 1.2em;
  background: #DFDFE0;
  border: 1px solid #DFDFE0;
  border-radius: 3px;
  color: #272733;
  outline: none;
  transition: outline 0.15s ease-in-out;
}
.form__input::placeholder {
  color: inherit;
}
.form__input_email {
  grid-area: email;
}
.form__input_name {
  grid-area: name;
}
.form__input_file {
  grid-area: file;
}
.form__input:focus-visible {
  outline: 2px solid #3E9CDC;
}
.form__label {
  position: relative;
}
.form__file-input {
  display: none;
}
.form__file-label {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  padding: 14px 0;
  height: 100%;
  color: #272733;
  background: #FFF;
  border-radius: 3px;
  transition: outline 0.15s ease-in-out;
  cursor: pointer;
}
.form__file-label:hover {
  outline: 2px solid #3E9CDC;
}
.form__file-icon {
  width: 17px;
  height: 17px;
}
.form__file-text {
  font-size: 15px;
  font-weight: 600;
  line-height: 1.2em;
  text-transform: uppercase;
}
.form__button {
  grid-area: button;
  padding: 14px;
  margin-top: 20px;
  font-size: 15px;
}

@media screen and (max-width: 992px) {
  .form__input {
    font-size: 16px;
  }
  .form__file-text {
    font-size: 13px;
  }
  .form__button {
    font-size: 13px;
  }
}
@media screen and (max-width: 768px) {
  .form {
    grid-template-columns: 1fr 1fr;
    grid-template-areas: "select-menu name" "email file" "slider slider" "button button";
    gap: 20px;
  }
}
@media screen and (max-width: 600px) {
  .form__input {
    padding: 10px;
    font-size: 13px;
  }
  .form__file-label {
    padding: 10px;
  }
  .form__file-icon {
    width: 14px;
    height: 14px;
  }
  .form__file-text {
    font-size: 11px;
  }
  .form__button {
